// Body
body {
	color: $text;
	margin: 0;
	padding: 50px 0 0 0;
	font-family: $body_font;
	font-size: 18px;
	font-weight: 300;
	background: $white;
}

.content {
	padding: 40px 0;
}

h2 {
	color: $highlight;
	font-weight: 300;
	margin: 0;
}
h3 {
	color: $title;
	font-weight: 300;
	margin: 0;
}
p {
	margin: 0 0 21px 0;
}
a {
	color: $highlight;
}
pre {
	padding: 20px;
	font-size: 20px;
	background-color: $pre;
}
.box {
	padding: 10px;
	margin: 0 0 20px 0;
	background: rgba(160,160,160,0.10);
}

.btn {
	display: inline-block;
	padding: 5px 10px 6px 10px;
	background-color: $blue;
	color: $white;
	text-decoration: none;
	border-radius: 4px;
	@include box-sizing();
	&:hover {
		background-color: darken($blue, 5%);
	}
}
section {
	margin-bottom: 50px;
}
.box p {
	margin: 0;
}
.code {
	font-family: "Courier New", serif;
	font-size: 14px;
}
.nesting {
	background: rgba(255,244,106,0.10);
	margin-bottom: 20px;
}
.nesting .nesting {
	background: rgba(223,251,191,0.30);
}

.content {
	border-bottom: 1px dotted $blue_lt;
}
.quote {
	background-color: $pre;
	p {
		font-size: 1.2em;
		font-style: italic;
	}
	.author {
		font-style: normal;
		font-size: .9em;
		text-align: right;
		margin: 0;
	}
	&#quote {
		padding: 20px 0;
	}
}

.foot {
	padding: 40px 0;
	background: $blue_dk;
	color: $white;
	font-size: 16px;
}

.warning {
	background-color: $warning_bg;
	border: 1px solid $warning;
	padding: 10px;
	border-radius: 10px;
}

@media (min-width: $bp-l) {

	body {
		font-size: 18px;
	}

}
@media (min-width: $bp-m) and (max-width: ($bp-l - 1)) {

	body {
		font-size: 16px;
	}

}
@media (min-width: $bp-s) and (max-width: ($bp-m - 1)) {
	
	body {
		font-size: 16px;
	}
	.content {
		padding: 20px 0 40px 0;
	}
	h2 {
		font-size: 24px;
		line-height: 24px;
		padding: 12px 0;
	}

	h3 {
		font-size: 21px;
		line-height: 21px;
		padding: 12px 0;
	}
	p {
		margin: 0 0 21px 0;
	}
	.btn {
		width: 100%;
		margin: 0 0 5px 0;
		text-align: center;
	}

}